import React, { useState } from 'react';
import DataForm from './DataForm';
import SearchData from './SearchData';

const Form = ({newName}) => {
    const [name, setName] = useState('');

    const [phone, setPhone] = useState('');

    const [Datas, setDatas] = useState([]);

    const [error, setError] = useState(false);


    function handleName(e) {
        setName(e.target.value);
    };

    function handlePhone(e) {
        setPhone(e.target.value);
    };

    function addData(name, phone, e) {
        if( name !== ''  && phone !== ''){
            const newRegister = {name: name, phone: phone, id: name + phone}
            setDatas([...Datas, newRegister])
            document.getElementById('form').reset()
            resetValues();
            setError(false)
        } else{
            setError(true)
        }
    }

    function resetValues(){
        setPhone('');
        setName('');
    }

    const dataList = Datas.map(Data => (
        <DataForm 
            id={Data.id}
            key={Data.id}
            name={Data.name}
            phone={Data.phone}
            setDatas={setDatas}
            Datas={Datas}
        />
    ))


    return (
        <div className="form">
            <form onSubmit={newName} id="form">
            <h1>CRUD</h1>
                <label htmlFor="username">
                    Name
                    <input 
                        type="text"
                        name="name"
                        id="username"
                        placeholder="Ex: Gabriel Jodas"
                        onChange={handleName}
                    />
                </label>

                <input 
                    type="tel"
                    name="cellphone"  
                    id="userphone" 
                    placeholder="Note"
                    pattern="[0-9]*$" 
                    onChange={handlePhone}
                    />
                    {error && 
                <span className="error">You need to fill both fields!</span>
                    }
                <button 
                    id="submitBtn" 
                    type="submit"
                    onClick={() => addData(name,phone)}>
                    Confirm
                </button>
            </form>           
            
                <SearchData 
                    Datas={Datas}
                    dataList={dataList}
                />            
        </div>
    )
}

export default Form;